html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  color: #0f0f3f; }

.header {
  width: 100%;
  height: 20vh;
  border-bottom: 2px solid #0066CC;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__logo {
  width: 31vh;
  height: 19.9vh; }

.header__nav--title {
  font-size: 1.7em; }

.header__nav {
  height: 10vh;
  display: flex;
  align-items: center;
  text-align: center; }

.header__nav--contact {
  width: 12vw;
  background-color: #0066CC;
  color: white;
  margin-right: 2vw;
  padding: 1.5vh;
  border-radius: 40px; }

.header__nav--contact:hover {
  cursor: pointer; }

.header__nav__button {
  color: white;
  text-decoration: none; }

.header__nav--join {
  width: 13vw;
  background-color: #0066CC;
  color: white;
  margin-right: 2vw;
  padding: 1.5vh;
  border-radius: 40px; }

.header__nav--join:hover {
  cursor: pointer; }

.menu__content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  list-style: none;
  text-align: center;
  font-size: 0.9em; }

.menu__content__item {
  width: 13.6vw; }

.menu__content__item__a {
  text-decoration: none;
  color: black; }

.menu__content__item__a:hover {
  cursor: pointer;
  color: #0066CC; }

.slider {
  height: 60vh;
  width: 100%;
  background-color: black; }

.section {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.section__title {
  text-align: center;
  border: none; }

.section__container {
  width: 90%;
  border-bottom: 2px solid #0066CC;
  padding: 3vh;
  text-align: center; }

.joinbutton {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10vh 0 10vh 0; }

.joinbutton__content {
  width: 15vw;
  height: 10vh;
  background-color: black;
  border-radius: 40px;
  border: none;
  font-size: 1.3em;
  position: absolute; }

.joinbutton__content__a {
  text-decoration: none;
  color: white; }

.joinbutton__content:hover {
  cursor: pointer; }

tr {
  background-color: #d6e6ee; }

.form {
  width: 50vw;
  color: white;
  background-color: #21292e;
  border-radius: 40px;
  margin: 5vh auto;
  padding: 1vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column; }

.send {
  width: 8vw;
  height: 8vh;
  margin: auto;
  padding: 1vh;
  border: 1px solid black;
  font-weight: bold;
  background-color: #d6e6ee; }

.send:hover {
  cursor: pointer; }

p {
  margin-bottom: 3vh; }

select {
  margin-top: 1vh; }

option {
  background-color: white;
  border-top: 3px solid white; }

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

.footer__img {
  flex-shrink: shrink; }
